<template>
    <div>
        <canvas ref="draw" class="panel"></canvas>
        <ul>
            <li>
                <van-button size="small" plain @click="selectFileHandler">照片</van-button>
                <input id="fileInput" type="file" accept="image/png,image/jpeg,image/jpg,image/bmp" style="display:none" v-on:change="fileChangeHandler">
            </li>
            <li>
                <van-button type="primary" size="small" @click="drawLineHandler">画线</van-button>
            </li>
            <li>
                <van-button type="primary" size="small" @click="drawTextHandler">文本</van-button>
            </li>
            <li>
                <van-button type="primary" size="small" @click="selectHandler">选择</van-button>
            </li>
            <li>
                <van-button type="primary" size="small" @click="translationHandler">平移</van-button>
            </li>
            <li>
                <van-button type="danger" size="small" @click="clearHandler">清除</van-button>
            </li>
            <li>
                <van-button type="primary" size="small" @click="saveHandler">保存</van-button>
            </li>
            <li>
                {{zoom}}
            </li>
        </ul>

        <van-slider v-model="zoom" :min="0.25" :max="3" :step="0.01" :vertical="true" @update:model-value="sliderChangeHandler" />
    </div>
</template>
<script>
import { fabric } from "fabric";
export default {
    name: "BaseFabric",
    data() {
        return {
            canvas: null,
            zoom: 1,
            isDragging: false,
            drawType: "line",
            mouseDown: false,
            lastPosX: 0,
            lastPosY: 0,
        };
    },
    mounted() {
        const element = this.$refs.draw;
        const rect = element.parentElement.getBoundingClientRect();
        element.setAttribute("width", rect.width + "px");
        element.setAttribute("height", rect.height + "px");

        this.$nextTick(() => {
            this.init(element);
        });
        // this.reset();
        document.addEventListener("keydown", (event) => {
            const code = window.event ? event.keyCode : event.which;
            if (code == 8) {
                //删除选中
                this.canvas.remove(this.canvas.getActiveObject());
            }
        });
    },
    methods: {
        init(element) {
            let canvas = (this.canvas = new fabric.Canvas(element, {
                isDrawingMode: false, //禁用自由绘图
            }));

            fabric.Object.prototype.transparentCorners = false;
            if (canvas.freeDrawingBrush) {
                canvas.freeDrawingBrush.color = "#ff0";
                canvas.freeDrawingBrush.width = 10;
                canvas.freeDrawingBrush.shadow = new fabric.Shadow({
                    blur: 0,
                    offsetX: 0,
                    offsetY: 0,
                    affectStroke: true,
                    color: "blue",
                });
            }

            console.log("canvas", canvas, this.$refs.draw);

            canvas.on("touch:gesture", (opt) => {
                console.log("touch:gesture", opt.e.type, opt.e.touches);
            });

            canvas.on("touch:drag", (opt) => {
                console.log("touch:drag", opt.e.type, opt.e.touches);
            });

            canvas.on("mouse:down", this.mouseDownHandler.bind(this));
            canvas.on("mouse:up", this.mouseUpHandler.bind(this));
            canvas.on("mouse:move", this.mouseMoveHandler.bind(this));
            this.drawLineHandler();
            /*  canvas.on("mouse:down", (opt) => {
                var evt = opt.e;
                if (evt.altKey === true) {
                    console.log("mouse:down", opt.e.type, opt);
                    this.isDragging = true;
                    this.canvas.selection = false;
                    this.lastPosX = evt.type == "touchstart" ? evt.targetTouches[0].clientX : evt.clientX;
                    this.lastPosY = evt.type == "touchstart" ? evt.targetTouches[0].clientY : evt.clientY;
                    console.log(this.lastPosX, this.lastPosY);
                }
            });

            canvas.on("mouse:move", (opt) => {
                if (this.isDragging) {
                    console.log("mouse:move", opt.e.type, opt);
                    var evt = opt.e;
                    var vpt = this.canvas.viewportTransform;
                    const clientX = evt.type == "touchmove" ? evt.targetTouches[0].clientX : evt.clientX;
                    const clientY = evt.type == "touchmove" ? evt.targetTouches[0].clientY : evt.clientY;
                    vpt[4] += clientX - this.lastPosX;
                    vpt[5] += clientY - this.lastPosY;
                    console.log(clientX, this.lastPosX, clientY - this.lastPosY);
                    this.canvas.requestRenderAll();
                    this.lastPosX = clientX;
                    this.lastPosY = clientY;
                }
            });

            canvas.on("mouse:up", (opt) => {
                console.log("mouse:up", opt.e.type, opt);
                if (this.isDragging) {
                    // 鼠标向上移动时，我们希望重新计算所有对象的新交互，因此我们称之为setViewportTransform
                    this.canvas.setViewportTransform(this.canvas.viewportTransform);
                    this.isDragging = false;
                    this.canvas.selection = true;
                }
            }); */

            canvas.on("mouse:wheel", ({ e }) => {
                var delta = e.deltaY;
                var zoom = canvas.getZoom();
                zoom *= 0.999 ** delta;
                if (zoom > 3) zoom = 3;
                if (zoom < 0.25) zoom = 0.25;
                this.zoom = zoom;
                // canvas.setZoom(zoom);
                canvas.zoomToPoint({ x: e.offsetX, y: e.offsetY }, zoom);
                e.preventDefault();
                e.stopPropagation();

                var vpt = this.canvas.viewportTransform;
                if (zoom < 400 / 1000) {
                    vpt[4] = 200 - (1000 * zoom) / 2;
                    vpt[5] = 200 - (1000 * zoom) / 2;
                } else {
                    if (vpt[4] >= 0) {
                        vpt[4] = 0;
                    } else if (vpt[4] < this.canvas.getWidth() - 1000 * zoom) {
                        vpt[4] = this.canvas.getWidth() - 1000 * zoom;
                    }
                    if (vpt[5] >= 0) {
                        vpt[5] = 0;
                    } else if (vpt[5] < this.canvas.getHeight() - 1000 * zoom) {
                        vpt[5] = this.canvas.getHeight() - 1000 * zoom;
                    }
                }
            });
        },
        mouseDownHandler(dataEvent) {
            console.log("down....", this.canvas.isDrawingMode, dataEvent);
            this.mouseDown = true;
            this.startTime = Date.now();
            this.mouseFrom = dataEvent.pointer;
            const evt = dataEvent.e;
            this.lastPosX = evt.type == "touchstart" ? evt.targetTouches[0].clientX : evt.clientX;
            this.lastPosY = evt.type == "touchstart" ? evt.targetTouches[0].clientY : evt.clientY;
        },
        mouseUpHandler(dataEvent) {
            console.log("up....", this.canvas.isDrawingMode, dataEvent);
            this.mouseDown = false;
            this.startTime = 0;
            this.mouseTo = dataEvent.pointer;
            if (this.drawType === "line") {
                this.startDrawing(dataEvent.e);
                this.canvasObject = null;
            } else if (this.drawType === "textbox") {
                if (!dataEvent.target) {
                    this.drawTextBox(dataEvent.e);
                }
                this.selection(false);
            } else if (this.drawType === "move") {
                this.canvas.setViewportTransform(this.canvas.viewportTransform);
            }
        },

        mouseMoveHandler(dataEvent) {
            if (this.mouseDown) {
                this.mouseTo = dataEvent.pointer;
                if (Date.now() - this.startTime >= 30 && this.drawType === "line") {
                    this.startTime = Date.now();
                    this.startDrawing(dataEvent.e);
                }

                if (this.drawType === "move") {
                    var evt = dataEvent.e;
                    var vpt = this.canvas.viewportTransform;
                    const clientX = evt.type == "touchmove" ? evt.targetTouches[0].clientX : evt.clientX;
                    const clientY = evt.type == "touchmove" ? evt.targetTouches[0].clientY : evt.clientY;
                    vpt[4] += clientX - this.lastPosX;
                    vpt[5] += clientY - this.lastPosY;
                    console.log(clientX, this.lastPosX, clientY - this.lastPosY);
                    this.canvas.requestRenderAll();
                    this.lastPosX = clientX;
                    this.lastPosY = clientY;
                }
            }
        },
        startDrawing() {
            if (this.canvasObject) {
                this.canvas.remove(this.canvasObject);
            }
            this.canvasObject = new fabric.Line([this.mouseFrom.x, this.mouseFrom.y, this.mouseTo.x, this.mouseTo.y], {
                fill: "#409EFF", //图形填充色
                stroke: "#F56C6C", //线条颜色
                strokeWidth: 2, //线条宽度
                strokeLineJoin: "round",
            });
            this.canvasObject.selectable = false;
            this.canvas.add(this.canvasObject);
        },
        drawTextBox(e) {
            if (["mouseup", "touchend"].indexOf(e.type) >= 0) {
                if (this.textBoxObject) {
                    this.textBoxObject.exitEditing();
                    this.canvas.discardActiveObject();
                    // if (this.textBoxObject.text == "") {
                    //     this.canvas(this.textBoxObject);
                    //     this.textBoxObject = null;
                    //     //TODO 处理图形被删除后的逻辑
                    // }
                }

                this.textBoxObject = this.createTextBox(this.mouseFrom, this.mouseTo, "");
                this.textBoxObject.setControlsVisibility({
                    bl: true, // 左下
                    br: true, // 右下
                    mb: false, // 下中
                    ml: true, // 中左
                    mr: true, // 中右
                    mt: false, // 上中
                    tl: true, // 上左
                    tr: true, // 上右
                    mtr: true, // 旋转控制键
                });

                console.log("这是文本框", this.textBoxObject);
                this.canvas.add(this.textBoxObject);
                this.textBoxObject.enterEditing();
                this.canvasObject.selectable = false;
                // this.canvas.setActiveObject(this.textBoxObject);
            }
        },
        createTextBox(from, to, text) {
            return new fabric.Textbox(text, {
                fontSize: 15, //字体大小
                left: from.x,
                top: from.y,
                fill: "#409EFF", //图形填充色
                originX: "left",
                width: to.x - from.x,
                height: to.y - from.y,
                backgroundColor: "rgba(83, 168, 255, 0.35)",
                editingBorderColor: "rgb(83, 168, 255)", //编辑框的边框色
                strokeWidth: 10,
                breakWords: true,
            });
        },
        sliderChangeHandler() {
            this.canvas.setZoom(this.zoom);
            this.canvas.requestRenderAll();
            if ("wx" in window) {
                window["wx"].showToast({
                    titile: "值" + this.zoom,
                });
            }
        },
        drawLineHandler() {
            this.drawType = "line";
            this.selection(false);
            if (this.textBoxObject) {
                this.textBoxObject.exitEditing();
            }
        },
        drawTextHandler() {
            this.drawType = "textbox";
            this.selection(false);
        },
        selectHandler() {
            this.drawType = "select";
            if (this.canvas) {
                this.selection(true);

                if (this.textBoxObject) {
                    this.textBoxObject.exitEditing();
                }
            }
        },
        translationHandler() {
            this.selectHandler();
            this.drawType = "move";
        },
        clearHandler() {
            this.canvas.clear();
        },
        selectFileHandler(event) {
            if ("wx" in window) {
                window["wx"].chooseImage({
                    count: 1,
                    success(res) {
                        console.log(res);
                        console.log(res.tempFilePaths);
                    },
                });
            } else {
                document.getElementById("fileInput").click(event);
            }
        },
        selection(selectable) {
            this.canvas.selection = false;
            this.canvas.skipTargetFind = false;
            this.canvas.discardActiveObject(); //清楚选中框
            this.canvas.getObjects().forEach((kclass) => {
                kclass.selectable = selectable;
            });
        },
        fileChangeHandler(event) {
            console.log("文件地址", event.target);
            console.log("this.files", event.target.files);
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                fabric.Image.fromURL(reader.result, (img) => {
                    img.scale(0.5).set({
                        left: 0,
                        top: 0,
                        angle: 0,
                        clipTo: function (ctx) {
                            ctx.arc(0, 0, 300, 0, Math.PI * 2, true);
                        },
                    });
                    // this.canvas.add(img).setActiveObject(img);
                    this.canvas.add(img);
                    this.drawLineHandler();
                });
            });
            reader.readAsDataURL(event.target.files[0]);
        },
        saveHandler() {
            const dataURL = this.canvas.toDataURL({
                width: this.canvas.width,
                height: this.canvas.height,
                left: 0,
                top: 0,
                format: "png",
            });

            window.postMessage(dataURL, "*");
            if ("wx" in window) {
                // window["wx"].miniProgram.postMessage({ data: dataURL });
                // window["wx"].miniProgram.navigateBack();
                window["wx"].downloadImage({
                    url: dataURL,
                    type: "image",
                    success(res) {
                        console.log("文件下载完成", res);
                    },
                    fail: function (res) {
                        console.log("download fail", res);
                    },
                    complete: function (res) {
                        console.log("download complete", res);
                    },
                });
                console.log("已经向微信小程序发送消息", dataURL);
            } else {
                console.warn("微信插件未加载");
                const a = document.createElement("a");
                a.style.display = "none";
                a.href = dataURL;
                a.download = "canvas.png";
                document.body.appendChild(a);
                a.click();
                setTimeout(() => {
                    document.body.removeChild(a);
                }, 100);
            }
        },
    },
};
</script>
<style scoped>
.panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: orange;
}

ul {
    position: absolute;
    list-style: none;
    padding: 0;
    margin: 0;
    right: 20px;
    bottom: 100px;
}

ul li {
    margin: 10px 0;
}

.van-slider {
    position: fixed;
    top: 50px;
    right: 30px;
    height: 300px;
}
</style>
